.playtime-container {
  flex-grow: 1;
}

.playtime-heading {
  font-family: 'Roboto';
  font-weight: 500;
  font-size: 24px;
  line-height: 1.42;
  color: #334155;
  margin: 0;
  text-align: center;
}

@media screen and (min-width: 768px) {
  .playtime-heading {
    font-size: 30px;
    line-height: 1.42;
    text-align: left;
  }
}

.playtime-description-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

@media screen and (min-width: 768px) {
  .playtime-description-container {
    flex-direction: row;
  }
}

.playtime-description {
  font-family: 'Roboto';
  font-size: 16px;
  line-height: 1.42;
  margin-top: 10px;
  width: 95%;
  max-width: 400px;
  text-align: center;
}

@media screen and (min-width: 768px) {
  .playtime-description {
    width: 60%;
    font-size: 18px;
    line-height: 1.42;
    margin-top: 15px;
    text-align: left;
  }
}

.playtime-games-image {
  width: 120px;
  height: 120px;
  order: -1;
  margin-top: 15px;
  margin-bottom: 15px;
}

@media screen and (min-width: 768px) {
  .playtime-games-image {
    width: 150px;
    height: 150px;
    margin-left: 20px;
    margin-right: 20px;
    order: 1;
  }
}
